import React from 'react';
import { Transition } from 'react-spring';
import styled from 'styled-components';

import { avatar } from '../../images';
import Emoji from '../Emoji';

const Ul = styled.ul`
  color: white;
  box-shadow: 5px 5px 5px rgba(0, 0, 0, 0.5);
  margin: auto;
  max-width: 600px;
`;

const Li = styled.li`
  display: flex;
  background-color: space-grey;
  margin: 0;
  transition: background-color 0.3s ease;
  &:nth-child(odd) {
    background-color: #323232;
  }
  &:nth-child(even) {
    background-color: #4b4b4b;
  }
  & img,
  & article {
    padding: 10px;
  }
`;

export default ({ event, eventHandler, rows }) => (
  <Ul>
    <Transition
      keys={rows.map(row => row.id)}
      from={{ transform: 'scale(0)', height: 0 }}
      enter={{ transform: 'scale(1)', height: 100 }}
      leave={{ transform: 'scale(0)', height: 0, pointerEvents: 'none' }}
    >
      {rows.map(row => styles => (
        <Li
          id={row.id}
          key={row.id}
          style={styles}
          {...{ [`${event}`]: eventHandler }}
          data-id={row.id}
        >
          <img src={avatar[row.id - 1]} alt="avatar" width="100" />
          <article>
            <div>
              <Emoji>üìá</Emoji>
              {row.firstName} {row.lastName}
            </div>
            <div>
              <Emoji>üìß</Emoji>
              {row.email}
            </div>
            <div>
              <Emoji>üè¢</Emoji>
              {row.company}
            </div>
          </article>
        </Li>
      ))}
    </Transition>
  </Ul>
);
